footer {
	clear: both;
	width:100%;
	background-color:$colorBlackTrue;
	padding:1rem 0 .5rem 0;
	position:relative;
.container{
			max-width:1280px;
			margin:10px auto;
			text-align:center;	
		}

ul {
	margin: 0;
	padding: 1rem;
	font-size:0;
	list-style-type:none;
	li {
		display: inline-block;
		margin: 1rem;
	
		a {
			font-size:1.2rem;
			font-family:monospace;
			text-transform:uppercase;
			color: $colorBlue;
			
			text-transform:lowercase;
			padding-right:.5rem;
			transition:color .3s ease;
		&:hover{
			color:$colorWhite;
		}

		}


	}
}

p {
	
text-align:center;
	

width:100%;
  margin:0;
  padding:0;
  color: $colorBlue;
  font-size:.8rem;
  padding:0 1rem 0 0;
  
}




}

